import React, { useEffect } from "react";
import useTooltip from "../hooks/useTooltip";
import waitlist_graph from "../imgs/waitlist_graph.png";
import survival_graph from "../imgs/survival_graph.png";
import survival_legend from "../imgs/survival_legend.png";
import Tooltip from "./Tooltip";
import Marker from "./Marker";

const SurvivalGraph = ({ epts, kdpi, excelData, markerSize }) => {
  const waitlistTooltip = useTooltip({
    x_min_percent: 0,
    x_max_percent: 100,
    y_min_percent: 0,
    y_max_percent: 100,
    excelData: excelData,
  });

  const survivalTooltip = useTooltip({
    x_min_percent: 0,
    x_max_percent: 100,
    y_min_percent: 0,
    y_max_percent: 100,
    excelData: excelData,
  });

  useEffect(() => {
    waitlistTooltip.handleMarkerCoord(epts, 1); //   
  }, [epts]); // epts kdpi   

  useEffect(() => {
    survivalTooltip.handleMarkerCoord(epts, kdpi); //   
  }, [epts, kdpi]); // epts kdpi   

  return (
    <div
      className="relative bg-white shadow-lg rounded-lg p-4 overflow-hidden w-full"
      style={{
        height: "400px",
      }}
    >
      <h2 className="text-lg font-semibold text-gray-700 mb-4 text-center">
        Waitlist vs. Post-Transplant Survival with Legend
      </h2>
      <br />
      <div
        className="flex h-full justify-center"
        style={{
          width: "100%",
          height: "calc(90% - 50px)",
        }}
      >
        {/* Waitlist Graph */}
        <div
          className="flex"
          style={{ width: "100px", height: "calc(100% - 50px)", marginRight: "20px" }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between", //     
              height: "calc(100% + 20px)", //   
              marginRight: "10px", //  
              marginTop: "-10px",
            }}
          >
            {[100, 80, 60, 40, 20, 0].map((num) => (
              <div
                key={num}
                style={{
                  fontSize: "12px",
                  color: "black",
                  textAlign: "right", //  
                }}
              >
                {num}
              </div>
            ))}
          </div>

          <div
            style={{
              backgroundImage: `url(${waitlist_graph})`,
              backgroundSize: "100% 100%", //    
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              width: "100%", //   
              height: "100%", //   
            }}
            onMouseMove={(event) =>
              waitlistTooltip.handleMouseMove(
                event,
                event.currentTarget.getBoundingClientRect()
              )
            }
            onMouseLeave={waitlistTooltip.handleMouseLeave}
          >
            <Marker left={50} top={waitlistTooltip.coord.y} width={markerSize} height={markerSize} />
            {/* "Waitlist" Text */}
            <div
              style={{
                position: "relative",
                top: "-40px", //   
                //left: "10%",
                //transform: "translateX(-50%)", //  
                fontSize: "14px",
                fontWeight: "bold",
                color: "black",
                textAlign: "center",
              }}
            >
              Waitlist
            </div>
          </div>
        </div>

        {/* Survival Graph */}
        <div
          className="relative w-full"
          style={{
            height: "100%", //   
            display: "grid", // CSS Grid 
            gridTemplateColumns: "20px 30px 20fr", // 3
            gridTemplateRows: "20fr 30px 20px", // 3
            gap: "0", //    
          }}
        >
          {/* Rotated Text */}
          <div
            style={{
              position: "relative",
              right: "0px", //   
              top: "50%",
              height: "20px",
              transform: "translateY(-50%) rotate(-90deg)", //  
              transformOrigin: "center",
              fontSize: "12px",
              fontWeight: "bold",
              color: "black",
              whiteSpace: "nowrap",
            }}
          >
            EPTS
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between", //     
              height: "calc(100% + 20px)", //   
              marginRight: "10px", //  
              marginTop: "-10px",
            }}
          >
            {[100, 80, 60, 40, 20, 0].map((num) => (
              <div
                key={num}
                style={{
                  fontSize: "12px",
                  color: "black",
                  textAlign: "right", //  
                }}
              >
                {num}
              </div>
            ))}
          </div>
          <div
            className="flex"
            style={{
              backgroundImage: `url(${survival_graph})`,
              backgroundSize: "100% 100%", //    
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              width: "100%", //   
              height: "100%", //   
            }}
            onMouseMove={(event) => { survivalTooltip.handleMouseMove(event, event.currentTarget.getBoundingClientRect()); }}
            onMouseLeave= {survivalTooltip.handleMouseLeave}
          >
            <Marker left={survivalTooltip.coord.x} top={survivalTooltip.coord.y} width={markerSize} height={markerSize} />
          </div>
          <div>{/* grid  2 1 */}</div>
          <div>{/* grid  2 2 */}</div>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between", //     
              width: "calc(100% + 10px)",
              height: "1%",
              marginTop: "5px", //  
              marginLeft: "-5px"
            }}
          >
            {[0, 20, 40, 60, 80, 100].map((num) => (
              <div
                key={num}
                style={{
                  fontSize: "12px",
                  color: "black",
                  textAlign: "center", //  
                }}
              >
                {num}
              </div>
            ))}
          </div>
          <div>{/* grid  3 1 */}</div>
          <div>{/* grid  3 2 */}</div>
          {/* Rotated Text */}
          <div
            style={{
              position: "relative",
              right: "calc(-50% + 15px)", //   
              top: "0",
              width: "30px",
              transformOrigin: "center",
              fontSize: "12px",
              fontWeight: "bold",
              color: "black",
              whiteSpace: "nowrap",
            }}
          >
            KDPI
          </div>
        </div>
        {/* Survival Legend ( ) */}
        <div
          className="flex"
          style={{ width: "100px", height: "calc(100% - 50px)", marginLeft: "20px" }}
        >
          <div
            style={{
              backgroundImage: `url(${survival_legend})`,
              backgroundSize: "100% 100%", //    
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              width: "30%", //   
              height: "100%", //   
            }}
          >
            {/* "Legend" Text */}
            <div
              style={{
                position: "relative",
                top: "-30px", //   
                //left: "80%",
                //transform: "translateX(-50%)", //  
                fontSize: "14px",
                fontWeight: "bold",
                color: "black",
                textAlign: "center",
              }}
            >
              Legend
            </div>
          </div>
          {/* Rotated Text */}
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between", //     
              height: "calc(100% + 20px)", //   
              marginLeft: "10px", //  
              marginTop: "-10px",
            }}
          >
            {[100, 80, 60, 40, 20, 0].map((num) => (
              <div
                key={num}
                style={{
                  fontSize: "12px",
                  color: "black",
                  textAlign: "left", //  
                }}
              >
                {num}
              </div>
            ))}
          </div>
          {/* Rotated Text */}
          <div
            style={{
              position: "relative",
              right: "-50px", //   
              top: "50%",
              transform: "translateY(-50%) rotate(-90deg)", //  
              transformOrigin: "center",
              fontSize: "12px",
              fontWeight: "bold",
              color: "black",
              whiteSpace: "nowrap",
            }}
          >
            10-year patient survival (%)
          </div>
        </div>
      </div>

      <Tooltip
        visible={waitlistTooltip.tooltip.visible}
        x={waitlistTooltip.tooltip.x}
        y={waitlistTooltip.tooltip.y}
        value={waitlistTooltip.tooltip.data?.wait}
      />

      <Tooltip
        visible={survivalTooltip.tooltip.visible}
        x={survivalTooltip.tooltip.x}
        y={survivalTooltip.tooltip.y}
        value={survivalTooltip.tooltip.data?.kidney}
      />
    </div>
  );
};

export default SurvivalGraph;
